/*------------------------------------*\
    VISIBILITY UTILITIES
\*------------------------------------*/


/* Screen reader
 -----------------------------------*/
.sr-only {
    position: absolute;
    overflow: hidden;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;

    clip: rect(0,0,0,0);
}



/* Centering
 -----------------------------------*/
.center-block {
    margin-right: auto;
    margin-left: auto;
}



/* Basic show/hide classes
 -----------------------------------*/
.hidden {
    display: none!important;
}
.show {
    display: block!important;
}

// Modernizer will add a hidden class to the HTML tag so this is here to counter that rule on the HTML tag
html.hidden {
    display: block!important;
}



/**
 * Responsive hide-below
 *
 * Uses media max-width so will use the value specified in _variables and will hide content
 * up to that point
 -----------------------------------*/

 .hide-below--small {
     display: none;
     @include breakpoint($small) {
         display: inline !important;
     }
 }


